<template>
  <div class="border border-gray-200 p-3 mb-4 rounded">
    <div v-show="!showform">
        <h4 class="inline-block text-2xl font-bold">{{ song.modified_name }}</h4>
          <button class="ml-1 py-1 px-2 text-sm rounded text-white bg-red-600 float-right
          " @click.prevent="deletesong">
                  <i class="fa fa-times"></i>
          </button>
          <button class="ml-1 py-1 px-2 text-sm rounded text-white bg-blue-600 float-right"
          @click.prevent="showform = !showform">
                  <i class="fa fa-pencil-alt"></i>
          </button>
    </div>
    <div v-show="showform">
        <div class="text-white text-center font-bold p-4 mb-4" v-if="show_alert"
        :class="alert_variant">
        {{ alert_message }}
        </div>
                <vee-form  :validation-schema ="EditSchema" :initial-values="song"
                @submit="edit">
                  <div class="mb-3">
                    <label class="inline-block mb-2">Song Title</label>
                    <vee-field type="text" name="modified_name"
                      class="block w-full py-1.5 px-3 text-gray-800 border border-gray-300
                        transition duration-500 focus:outline-none focus:border-black rounded"
                      placeholder="Enter Song Title"
                      @input="updateunsavedflag(true)" />
                      <ErrorMessage class="text-red-600" name="modified_name" />
                  </div>
                  <div class="mb-3">
                    <label class="inline-block mb-2">Genre</label>
                    <vee-field type="text" name="genre"
                      class="block w-full py-1.5 px-3 text-gray-800 border border-gray-300
                        transition duration-500 focus:outline-none focus:border-black rounded"
                      placeholder="Enter Genre"
                      @input="updateunsavedflag(true)" />
                      <ErrorMessage class="text-red-600" name="genre" />
                  </div>
                  <button type="submit" class="py-1.5 px-3 mr-1 rounded text-white bg-green-600"
                  :disabled="in_subb">
                    Submit
                  </button>
                  <button type="button" class="py-1.5 px-3 ml-1  rounded text-white bg-gray-600"
                  :disabled="in_subb" @click.prevent="showform = false">
                    Go Back
                  </button>
                </vee-form>
    </div>
  </div>
</template>

<script>
import { songscollection, storage } from '@/includes/firebase';

export default {
  name: 'compositionitem',
  props: {
    song: {
      type: Object,
      required: true,
    },
    updateSong: {
      type: Function,
      required: true,
    },
    index: {
      type: Number,
      required: true,
    },
    removesong: {
      type: Function,
      required: true,
    },
    updateunsavedflag: {
      type: Function,
    },
  },
  data() {
    return {
      EditSchema: {
        modified_name: 'required',
        genre: 'alpha_spaces',
      },
      showform: false,
      in_subb: false,
      show_alert: false,
      alert_variant: 'bg-blue-500',
      alert_message: 'Please wait! Updating song info.',
    };
  },
  methods: {
    async edit(values) {
      this.in_subb = true;
      this.show_alert = true;
      this.alert_variant = 'bg-blue-500';
      this.alert_message = 'Please wait! Updating song info.';
      try {
        await songscollection.doc(this.song.docID).update(values);
      } catch (error) {
        this.in_subb = false;
        this.alert_variant = 'bg-red-500';
        this.alert_message = 'Something went wrong! Try again later';
        return;
      }

      this.updateSong(this.index, values);
      this.updateunsavedflag(false);
      this.in_subb = false;
      this.alert_variant = 'bg-green-500';
      this.alert_message = 'Success!';
      console.log('song editted');
    },
    async deletesong() {
      const storageRef = storage.ref();
      const songref = storageRef.child(`songs/${this.song.original_name}`);

      await songref.delete();
      await songscollection.doc(this.song.docID).delete();

      this.removesong(this.index);
    },
  },
};
</script>
