<template>
    <!-- Main Content -->
  <main>
    <section class="container mx-auto mt-6">
      <div class="md:grid md:grid-cols-3 md:gap-5 pl-6 pr-6">
        <div class="col-span-1">
          <!-- Upload component -->
          <AppUpload ref="upload" :addsong="addsong"/>
        </div>
        <div class="col-span-2">
          <div class="bg-white rounded border border-gray-200 relative flex flex-col">
            <div class="px-6 pt-6 pb-5 font-bold border-b border-gray-200">
              <span class="card-title">My Songs</span>
              <i class="fa fa-compact-disc float-right text-green-400 text-2xl"></i>
            </div>
            <div class="p-6">
              <!-- Composition Items -->
              <compositionitem v-for="(song, i) in songs" :key="song.docID"
              :song="song" :updateSong="updateSong"
              :index="i" :removesong="removesong"
              :updateunsavedflag="updateunsavedflag"/>
            </div>
          </div>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
// import store from '@/store';
import AppUpload from '@/components/Upload.vue';
import { songscollection, auth } from '@/includes/firebase';
import compositionitem from '@/components/compositionitem.vue';

export default {
  name: 'manage',
  components: {
    AppUpload, compositionitem,
  },
  data() {
    return {
      songs: [],
      unsavedflag: false,
    };
  },
  async created() {
    const snapshot = await songscollection.where('uid', '==', auth.currentUser.uid).get();
    snapshot.forEach(this.addsong);
  },
  methods: {
    updateSong(i, values) {
      this.songs[i].modified_name = values.modified_name;
      this.songs[i].genre = values.genre;
    },
    removesong(i) {
      this.songs.splice(i, 1);
    },
    addsong(doc) {
      const song = {
        ...doc.data(),
        docID: doc.id,
      };
      this.songs.push(song);
    },
    updateunsavedflag(value) {
      this.unsavedflag = value;
    },
  },
  beforeRouteLeave(to, from, next) {
    if (!this.unsavedflag) {
      next();
    } else {
      // eslint-disable-next-line no-alert, no-restricted-globals
      const leave = confirm('You have unsaved changes. Are you sure you want to leave?');
      next(leave);
    }
  },
//   beforeRouteEnter(to, from, next) {
//     console.log(store.state.userLoggedIn);
//     if (store.state.userLoggedIn) {
//       next();
//     } else {
//       // eslint-disable-next-line no-alert
//       // alert('Log in to your account to continue..');
//       next({ name: 'home' });
//     }
//   },
};
</script>
